.app {
  padding-top: 70px;
}

.results {
  display: flex;
  align-items: center;
  gap: 25px;
  justify-content: center;
}

.results--item {
  background-color: black;
  padding: 20px;
  color: white;
  font-size: 24px;
  font-weight: bold;
}

.label {
  margin-right: 30px;
  font-size: 34px;
  font-weight: bold;
}

.entries {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 60px;
  flex-direction: column;
}

.entries--item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
}

.entries--item button {
  font-size: 24px;
  padding: 15px;
}

.constants {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 50px 20px;
  font-size: 18px;
}

@media (max-width: 600px) {
  .results--item {
    padding: 20px;
    font-size: 24px;
  }
  
  .label {
    margin-right: 10px;
    font-size: 18px;
  }
  
  .entries {
    gap: 60px;
  }
  
  .entries--item {
    gap: 10px;
  }
  
  .entries--item button {
    font-size: 18px;
    padding: 5px;
  }
}